<template>
    <div class="me">
        <div class="container">
            <div class="row">
                <div class="col-lg-2 offset-lg-5 col-6 offset-3">
                    <div class="avatar">
                        <img :src="info.avatar">
                    </div>
                    <h5 class="text-center">{{info.name}}</h5>
                </div>
            </div>
            <div class="row">
                <div class="col-lg-6 offset-lg-3">
                    <div class="info">
                        <p>一个身在武汉的IT民工，躲在一个不到10平方米的角落。十足的猫奴，目前没有养猫，在未来会养。喜欢编程、写信、集邮、电影。会玩一些电子游戏，Minecraft忠实爱好者。</p>
                        <p>头像是在一个冬天的夜晚拍摄的，天还下着雪。</p>
                        <p>&nbsp;</p>
                        <p>&nbsp;</p>
                        <p>&nbsp;</p>
                        <p>&nbsp;</p>
                        <p>&nbsp;</p>
                        <p>&nbsp;</p>
                        <p>&nbsp;</p>
                        <p>博客初尝试，先写到这里。</p>
                        <p class="text-end">2021年4月23日</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            info: {}
        }
    },
    mounted() {
        this.$axios.get("/static/mock/index.json").then(res => {
            this.info = res.data.me
        })
    }
}
</script>

<style scoped>
    .me {
        margin: 2rem 0;
    }
    .avatar {
        aspect-ratio: 1;
        border: 1px solid #f1f1f1;
        padding: 10px;
        margin-bottom: 2rem;
    }
    .avatar img {
        margin: auto;
        vertical-align: middle;
        display: inline-block;
        max-width: 100%;
        max-height: 100%;
    }
    .info {
        margin-top: 1rem;
    }
    p {
        text-align: justify;
        text-indent: 2rem;
        margin: 0;
    }
</style>


